import"core-js/modules/es.array.map.js";import{Set as ImmutableSet}from'immutable';import ConfigDB from'soapbox/utils/config-db';import{fetchConfig,updateConfig}from'./admin';var simplePolicyMerge=function simplePolicyMerge(simplePolicy,host,restrictions){return simplePolicy.map(function(hosts,key){var isRestricted=restrictions.get(key);if(isRestricted){return ImmutableSet(hosts).add(host);}else{return ImmutableSet(hosts).delete(host);}});};var updateMrf=function updateMrf(host,restrictions){return function(dispatch,getState){return dispatch(fetchConfig()).then(function(){var configs=getState().admin.get('configs');var simplePolicy=ConfigDB.toSimplePolicy(configs);var merged=simplePolicyMerge(simplePolicy,host,restrictions);var config=ConfigDB.fromSimplePolicy(merged);return dispatch(updateConfig(config.toJS()));});};};export{updateMrf};